.window {
  border: 1px solid var(--theme);
  border-radius: 10px;
  margin: 12px;
  -ms-flex: 1 1 100%;
      flex: 1 1 100%;
  min-width: 0px;
}

.window-content {
  margin: 12px;
}

.window h3 {
  background-color: var(--theme);
  color: white;
  border-radius: 10px 10px 0px 0px;
  padding: 4px 8px;
}

@media (min-width: 960px) {
  .window {
    min-width: 400px;
  }

  .window-left {
    -ms-flex: 0 1 1px;
        flex: 0 1 1px;
    width: 360px;
  }

  .window-center {
    max-width: 420px;
    margin: 12px auto;
  }
}



